import React, { useEffect, useState } from "react";
import { Anchor } from "grommet";
import { searchObject } from "../library/parse";

const Component = () => {
  const [store, setStore] = useState(undefined);

  useEffect(() => {
    searchObject("Store", {
      limit: 1,
    }).then((store) => store[0] && setStore(store[0]));
  }, []);

  return (
    <>
      <p>Welcome to bidbid.live</p>
      {store === null && (
        <>
          <Anchor href="/setupstore">Setup</Anchor> a store to get started.
        </>
      )}
      {store && (
        <>
          <p>
            <Anchor href={`/store/${store.get("uniqueName")}`}>Upload</Anchor>{" "}
            products to {store.get("name")} store to start selling.
          </p>
          <p>
            <Anchor href={`/profile`}>Update</Anchor> your contact info to start
            bidding.
          </p>
        </>
      )}
    </>
  );
};

export default Component;
